	module.exports = {"data":{"site":{"siteMetadata":{"title":"FROG Blog","author":"FROG Team"}},"markdownRemark":{"id":"/Users/prastutkumar/Dev/EPFL/blog/src/pages/visual-makeover/index.md absPath of file >>> MarkdownRemark","html":"<h1>Overview</h1>\n<p>FROG’s editor aims to enable a teacher or researcher to configure a complex learning flow, including multiple activities, on multiple social planes, with social groupings and data flow. The orchestration view acts as the “driver’s seat” during the class, offering controls and data visualizations to enable the teacher to be aware of what is going on with multiple students working alone and in groups with multiple activities. These are all complex information representation and user interface challenges, and there is much room for improvement.</p>\n<p>We are currently engaged in a process of redesigning our interface</p>\n<h1>Goal</h1>\n<p>To make FROG more user friendly, specifically for teachers.</p>","frontmatter":{"title":"FROG gets a visual makeover!","date":"May 27, 2018"}}},"pathContext":{"slug":"/visual-makeover/","previous":null,"next":null}}

